import React from "react";
import userImageDefault from '../../../assets/images/userImageDefault.png';
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import s from './ProfileInfo.module.css';

const ProfileInfo = ({profile, updateStatus, status, isOwner, saveImage}) => {
    const onImageSelected = (e) => {
        saveImage(e.target.files[0]);
    };

    return (
        <div>
            <h1>{profile.fullName}</h1>
            <img className={s.profileImage} src={profile.photos.large != null ? profile.photos.large : userImageDefault}/>
            {isOwner ? <input type={'file'} onChange={onImageSelected} /> : null}
            <ProfileStatusWithHooks updateStatus={updateStatus} status={status}/>
        </div>
    );
}

export default ProfileInfo;